import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router'; // For routes
import { FormsModule } from '@angular/forms'; // For data handling in UI
import { HttpClientModule } from '@angular/common/http'; // For Posting and getting the data from UI

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { AppRoutingModule } from './app-routing.module';
import { RegisterComponent } from './register/register.component';
import { LogoutComponent } from './logout/logout.component';
import { ThreedinterfaceComponent } from './threedinterface/threedinterface.component';
import { AdminPanelComponent } from './admin-panel/admin-panel.component';
import { DialogComponent } from './shared/dialog/dialog.component';


const routes: Routes = [
    { path: 'login', component: LoginComponent },
    { path: 'dashboard', component: DashboardComponent },
    { path: 'logout', component: LogoutComponent},
    { path: 'register', component: RegisterComponent },
    { path: 'admin', component: AdminPanelComponent },
    { path: 'threedinterface', component: ThreedinterfaceComponent }
  ];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    DashboardComponent,
    LoginComponent,
    RegisterComponent,
    LogoutComponent,
    ThreedinterfaceComponent,
    AdminPanelComponent,
    DialogComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    AppRoutingModule,
    FormsModule,
    HttpClientModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



