import { Component, OnInit } from '@angular/core';
import * as THREE from 'three/build/three.min.js';
import { CSS3DRenderer } from 'three/examples/js/renderers/CSS3DRenderer.js';
import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls.js';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  	this.main();
}

	main() {
	  	var scene = new THREE.Scene();
		var camera = new THREE.PerspectiveCamera( 60, window.innerWidth/window.innerHeight, 0.1, 10000 );

		 // WEBGL Renderer
	    var renderer = new THREE.WebGLRenderer( { antialias: true } );
		renderer.setPixelRatio( window.devicePixelRatio );
		renderer.setSize( window.innerWidth, window.innerHeight );
		document.body.appendChild( renderer.domElement );

		scene.background = new THREE.Color( 0x000000 );
		scene.fog = new THREE.FogExp2( 0xcccccc, 0.002 );

		const geometry = new THREE.CylinderGeometry( 0, 10, 30, 4, 1 );
		const material = new THREE.MeshPhongMaterial( { color: 0x05719D, flatShading: true } );

		for ( let i = 0; i < 500; i ++ ) {
			const mesh = new THREE.Mesh( geometry, material );
			mesh.position.x = ( Math.random() - 0.5 ) * 1000;
			mesh.position.y = ( Math.random() - 0.5 ) * 1000;
			mesh.position.z = ( Math.random() - 0.5 ) * 1000;
			mesh.updateMatrix();
			mesh.matrixAutoUpdate = false;
			scene.add( mesh );
		}

		var geometry1 = new THREE.BoxGeometry( 10, 10, 10 );
		var material1 = new THREE.MeshStandardMaterial( { color: 0x9fdf9f, wireframe:false } );
		var cube = new THREE.Mesh( geometry1, material1 );
		scene.add( cube );

		const geometry2 = new THREE.PlaneGeometry( 500, 500, 500 );
		const material2 = new THREE.MeshStandardMaterial( {
				color: 0xe6e6ff,
				side: THREE.DoubleSide,             
                roughness: 0.0,
                metalness: 0.0} 
                );
		const plane = new THREE.Mesh( geometry2, material2 );
		plane.receiveShadow = true;
		scene.add( plane );
		plane.rotation.x = Math.PI/2;

		var light = new THREE.PointLight(0xFFFFFF, 0.1);
        light.castShadow = true;
        scene.add(light);
        light.position.z=300;

		camera.position.z = 1000;

		let controls = new TrackballControls( camera, renderer.domElement );
		controls.rotateSpeed = 5.0;
		controls.zoomSpeed = 3.2;
		controls.panSpeed = 3;
		controls.keys = [ 65, 83, 68 ];
				

		window.addEventListener( 'resize', onWindowResize, false );

		function onWindowResize(){

		    camera.aspect = window.innerWidth / window.innerHeight;
		    camera.updateProjectionMatrix();

		    renderer.setSize( window.innerWidth, window.innerHeight );

		    controls.handleResize();

		}

		var animate = function () {
		  requestAnimationFrame( animate );

		  // cube.rotation.y += 0.01;

		  controls.update();

		  renderer.render( scene, camera );
		};

		animate();

	  }
	}


