import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
  providers: [AuthService]
})
export class RegisterComponent implements OnInit {

  employees: any = [];
  isShowEmployeeClicked: boolean = false;
  viewAlertBox = false;

  constructor(
  	private auth: AuthService,
  	private router: Router
  	) { }

  ngOnInit() {
  }

  showEmployees() {
	  console.log(this.employees);
	  this.isShowEmployeeClicked = true;
  }

  viewEmployees(){
	this.employees = [];
	this.auth.viewEmployees().subscribe(data => {
		console.log(data);
		data.forEach(element => {
			console.log(element.username);
			this.employees.push(element.username);
		});
	});
	this.isShowEmployeeClicked = false;
  }

  registerUser(event) {
	this.viewAlertBox = true;
  	event.preventDefault();
  	const errors = [];
  	const target = event.target;
  	const username = target.querySelector('#username').value;
  	const password = target.querySelector('#password').value;
  	const cpassword = target.querySelector('#cpassword').value;

  	if(password !== cpassword ) {
  		errors.push("Passwords do not match");
      console.log(errors);
  	}

  	if(errors.length === 0){

			this.auth.registerUser(username,password).subscribe(data => {
				console.log(data);
				if(data.success){
					this.router.navigate(['dashboard']);
				}
			}) 

  	}

  	console.log(username,password);

  }

  closeAlertBox() {
	this.viewAlertBox = false;
  }

  ngOnDestroy() {
  }

}



